.barWrapper {
  width: 80px;
  height: 90px;
  display: flex;
  flex-direction: column;
  margin: auto;
}

.fullBar {
  background-color: gray;
  border-radius: 13px;
  height: 90px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.barFilled {
  border-radius: 13px;
  width: 80px;
  font-weight: 600;
  box-shadow: 2px 3px 5px 0px rgba(0, 0, 0, 0.25);
  transition: transform .2s ease-in-out;
  padding-top: 2px;
}

.barFilled:hover {
  transform: scale(1.03);
}

.barRest {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 4px;
  font-weight: 600;
}

.subBar {
  margin-top: auto;
  display: flex;
  justify-content: center;
  padding-bottom: 6px;
}

.subBar>* {
  padding-left: 5px;
  padding-right: 5px;
}

.fullWrapper {
  height: 100%;
  text-align: center;
  display: flex;
  flex-direction: column;
}
