.barWrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 23px;
}

.markedRow {
  background-color: #F2F2F2;
  border-radius: 13px;
}

.rank {
  padding-left: 8px;
  flex-basis: 20px;
}

.medal {
  flex-basis: 20px;
}

.name {
  flex-basis: 120px;
  overflow: hidden;
}

.fullBar {
  flex: 1;
  display: flex;
  margin-left: auto;
  background-color: gray;
  border-radius: 13px;
}

.barFilled {
  border-radius: 13px;
  text-align: right;
  padding-right: 12px;
  font-weight: 600;
  box-shadow: 2px 3px 5px 0px rgba(0, 0, 0, 0.25);
  transition: transform .2s ease-in-out;
}

.barFilled:hover {
  transform: scale(1.03);
}

.barRest {
  display: flex;
  padding-left: 8px;
}

.stepsTooSmall {
  padding-top: 2px;
  padding-right: 4px;
  font-weight: 600;
}
