.wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  position: absolute;
}

.wrapper>* {
  text-align: center;
}

.mainContent {
  display: flex;
  width: 100%;
  height: 100%;
  padding-left: 13px;
  padding-right: 13px;
}

.mainContent>* {
  text-align: center;
}

.left {
  flex: 3 1 0;
  padding-left: 20px;
}

.center {
  flex: 1 1 0;
  align-self: center;
  font-weight: 500;
  font-size: 26px;
}

.right {
  flex: 3 1 0;
  padding-right: 20px;
}

.downPart {
  margin-top: auto;
  bottom: 0;
}



.ui.button.buttonSelected {
  background-color: white !important;

}

/* 
::-webkit-selection{background-color:#CCE2FF;color:rgba(0,0,0,.87);}
::-moz-selection{background-color:#CCE2FF;color:rgba(0,0,0,.87);}
::selection{background-color:#CCE2FF;color:rgba(0,0,0,.87);} */
.ui.button {
  flex: 1 0 auto;
  cursor: pointer;
  display: inline-block;
  min-height: 1em;
  outline: 0;
  border: none;
  vertical-align: baseline;
  background: #F2F2F2;
  color: rgba(0, 0, 0, .6);
  font-family: Lato, 'Helvetica Neue', Arial, Helvetica, sans-serif;
  padding: .78571429em 1.5em;
  text-transform: none;
  text-shadow: none;
  font-weight: 700;
  line-height: 1em;
  font-style: normal;
  text-align: center;
  text-decoration: none;
  border-radius: 0;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34, 36, 38, .15) inset;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-transition: opacity .1s ease, background-color .1s ease, color .1s ease, box-shadow .1s ease, background .1s ease;
  transition: opacity .1s ease, background-color .1s ease, color .1s ease, box-shadow .1s ease, background .1s ease;
  will-change: '';
  -webkit-tap-highlight-color: transparent;
}

.ui.button:hover {
  background-color: #cacbcd;
  background-image: none;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34, 36, 38, .15) inset;
  color: rgba(0, 0, 0, .8);
}

.ui.button:focus {
  background-color: #CACBCD;
  color: rgba(0, 0, 0, .8);
  background-image: '' !important;
  box-shadow: '' !important;
}

.ui.button:active {
  background-color: #BABBBC;
  background-image: '';
  color: rgba(0, 0, 0, .9);
  box-shadow: 0 0 0 1px transparent inset, none;
}

.ui.button:disabled {
  cursor: default;
  opacity: .45 !important;
  background-image: none !important;
  box-shadow: none !important;
  pointer-events: none !important;
}

.ui.button,
.ui.buttons .button {
  font-size: 1rem;
}

.ui.attached.buttons .button {
  margin: 0;
}

.ui.attached.buttons .button:last-child {
  border-radius: 0;
}

.ui.button.lastButton {
  border-radius: 0 0 .28571429rem;
}

.ui.button.firstButton {
  border-radius: 0 0 0 .28571429rem;
}

.ui.buttons:not(.basic):not(.inverted)>.button,
.ui.buttons>.ui.button:not(.basic):not(.inverted) {
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34, 36, 38, .15) inset;
}

.ui.buttons .button:last-child {
  border-top-right-radius: .28571429rem;
  border-bottom-right-radius: .28571429rem;
}
